import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { WorkComponent } from './work/work.component';
import { CrematoriumComponent } from './crematorium/crematorium.component';
import { DigEngagementComponent } from './dig-engagement/dig-engagement.component';
import { AccessibilityComponent } from './accessibility/accessibility.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'work', component: WorkComponent },
  { path: 'crematorium', component: CrematoriumComponent },
  { path: 'dig-engagement', component: DigEngagementComponent },
  { path: 'accessibility', component: AccessibilityComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
